.container {
    width: 100%;
    /* display: block; */
    height: 100%;
    -webkit-order: 2;
            order: 2;
    -webkit-flex: 2 1;
            flex: 2 1;
}

@media only screen and (max-aspect-ratio: 8/7) {
    .container {
        -webkit-order: 1;
                order: 1;
    }
}

html,
body,
#root,
main {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0px;
  background-color: #f0efeb;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.canvas, .loader {
  width: 100%;
  /* display: block; */
  height: 100%;
  -webkit-order: 2;
          order: 2;
  -webkit-flex: 2 1;
          flex: 2 1;
}

.overlay {
  position: relative;
  -webkit-order: 1;
          order: 1;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding-left: 50px;
}

.menu-svg {
  width: 40vw;
  height: 100vh;
}

/* .name {
  font-size: 86px;
} */

@media only screen and (max-aspect-ratio: 8/7) {
  html,
  body,
  #root,
  main {
    padding: 0;
  }

  main {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .canvas, .loader {
    -webkit-order: 1;
            order: 1;
  }

  .overlay {
    width: 100%;
    padding-left: 0;
  }

  .menu-svg {
    -webkit-order: 2;
            order: 2;
    width: 100%;
    max-width: 100%;
    height: 50vh;
  }
}
